@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.nav-button{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:0;width:100%;height:52px;border:0;background-color:#414042;color:white;cursor:pointer}.nav-button__img{margin-left:30px;margin-right:20px}.nav-button__arrow{position:absolute;right:25px;font-size:18px;transition:all 0.3s}.nav-button__arrow--active{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.nav-link{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;height:52px;color:white;text-decoration:none}.nav-link--small{-webkit-justify-content:center;justify-content:center}.nav-link--active{background-color:#188ae2}.nav-link__img{height:14px;width:18px;margin-left:30px;margin-right:20px}

.nav{position:-webkit-sticky;position:sticky;top:0;padding-top:17px}.nav__help-support{position:relative;display:-webkit-flex;display:flex;margin-top:55px;color:white;text-decoration:none}.nav__help-support__img{margin-right:20px;margin-left:30px}.nav__help-support::before{position:absolute;top:-54px;content:" ";height:1px;width:100%;background-color:#313131}

.rating-box{height:100px;padding-left:35px;padding-top:17px;padding-right:29px;color:#f2f3f6}.rating-box__title{font-size:14px;font-weight:normal}.rating-box__number{margin-right:15px;font-size:28px;font-weight:500}.rating-box__text{font-size:14px}.rating-box__rating{width:100%;height:5px;margin-top:5px;background-color:#414042;border-radius:8px}.rating-box__line{height:5px;background-color:#fff;border-radius:8px}

.rating-boxes{margin-bottom:17px;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:17px;gap:17px}

.events{margin-bottom:20px;height:260px;padding:40px;background-color:#fff}

.weather{height:260px;background-color:blue}

.dashboard{width:calc(100% - 240px);padding:30px}.dashboard--small{width:calc(100% - 240px - 290px)}.dashboard__wrapper-visitors{display:grid;grid-template-columns:2.05fr 1fr;grid-gap:17px;gap:17px}.dashboard__visitors{height:540px;background-color:red}.dashboard__top-order-wrapper{height:400px;display:grid;grid-template-columns:1fr 1fr;grid-gap:17px;gap:17px}

@-webkit-keyframes bell-animation{50%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(30deg);transform:rotate(30deg)}}@keyframes bell-animation{50%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(30deg);transform:rotate(30deg)}}.header{display:-webkit-flex;display:flex;height:80px;background-color:#188ae2}.header__nav{width:290px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:0 20px;background-color:#313131}.header__nav__title{font-size:23px;color:#f1f2f2;font-weight:normal}.header__nav__burger{height:40px;background-color:#313131;border:0;cursor:pointer}.header__burger-line{height:3px;width:42px;margin-bottom:7px;background-color:white;border-radius:10px}.header__burger-button{background-color:#188ae2;border:0;margin-top:25px;margin-left:30px;cursor:pointer}.header__main{width:100%;padding:0 30px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.header__main--small{width:calc(100% - 290px)}.header__main__left-box{display:-webkit-flex;display:flex}.header__main__right-box{display:-webkit-flex;display:flex}.left-box{position:relative}.left-box__input{width:215px;height:35px;padding-left:20px;margin:auto;background-color:#0b76c9;border-radius:20px;border:0;color:white}.left-box__input::-webkit-input-placeholder{color:white;font-size:14px}.left-box__input:-ms-input-placeholder{color:white;font-size:14px}.left-box__input::placeholder{color:white;font-size:14px}.left-box__input-logo{position:absolute;top:31px;right:14px;height:20px;width:24px}.right-box__settings-button{height:27px;width:27px;margin:auto;margin-left:27px;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-position:center;background-size:15px 15px;border:1px white solid;border-radius:5px;cursor:pointer;background-color:#188ae2}.right-box__bell{position:relative;margin:auto;margin-left:30px}.right-box__active-messages{position:absolute;top:-10px;right:-4px;height:16px;width:16px;background-color:#ff5b5b;border-radius:50%;cursor:pointer;color:white;font-size:10px;text-align:center;line-height:16px}.right-box__bell-img--active{-webkit-animation:bell-animation 0.6s ease infinite alternate;animation:bell-animation 0.6s ease infinite alternate}.person{margin:auto}.person__title{font-size:14px;color:white;font-weight:normal}.person__position{font-size:10px;color:white}

.new-messages{padding-bottom:55px}.new-messages__title{margin-top:30px;margin-bottom:15px;margin-left:27px;font-size:14px;font-weight:normal;color:#414042}.new-messages__item{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-left:25px;margin-bottom:20px}.new-messages__img{height:42px;width:42px;margin-right:17px;border-radius:50%}.new-messages__name{color:#414042;font-size:14px;font-weight:normal}.new-messages__online{font-size:12px;color:#414042}

.chat-line__title{margin-top:30px;margin-bottom:15px;margin-left:27px;font-size:14px;font-weight:normal;color:#414042}.chat-line__item{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-left:25px;margin-bottom:20px}.chat-line__img{height:42px;width:42px;margin-right:17px;border-radius:50%}.chat-line__name{color:#414042;font-size:14px;font-weight:normal}.chat-line__circle{position:absolute;right:27px;top:8px;height:10px;width:10px;background-color:#ff5b5b;border-radius:50%}.chat-line__circle--online{background-color:#10c469}

.right-bar{position:-webkit-sticky;position:sticky;top:0;width:240px;height:100vh;background-color:#e6e7e8}

.app__wraper{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.app__nav{width:290px;background-color:#414042}

*,*::before,*::after{box-sizing:border-box;outline:none}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul,ol{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*{margin-top:1em}input,button,textarea,select{font:inherit}ul{margin:0;padding:0}

*{font-family:"Roboto", sans-serif;font-size:12px}

